@font-face {
  font-family: "PT-Mono";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/pt-mono-400.woff2") format("woff2"), url("../fonts/pt-mono-400.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Roboto-Condensed";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/roboto-condensed-400.woff2") format("woff2"), url("../fonts/roboto-condensed-400.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Roboto-Condensed";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/roboto-condensed-700.woff2") format("woff2"), url("../fonts/roboto-condensed-700.woff") format("woff");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "PT-Mono", "Times New Roman", serif;
  color: #ffffff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
  scroll-behavior: smooth;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #ffffff;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.clear-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.container {
  width: 100%;
  max-width: 1220px;
  min-width: 320px;
  margin: 0 auto;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 50px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 25px;
  }
}

.button {
  display: block;
  margin: 0;
  border: none;
  border-radius: 3px;
  cursor: pointer;
  user-select: none;
  font-family: "Roboto-Condensed", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 14px;
  letter-spacing: 0.05em;
  text-align: center;
  text-transform: uppercase;
}
@media (hover: hover) {
  .button:hover:not(.focus-visible) {
    opacity: 0.7;
  }
  .button:hover:not(.focus-visible):active {
    opacity: 1;
  }
}
.button.focus-visible:focus {
  opacity: 0.7;
}
.button.focus-visible:focus:active {
  opacity: 1;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(3, 16, 27, 0.7);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: start;
  width: 100%;
  min-height: 100%;
  padding: 145px 0 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 500px;
  min-height: 610px;
  padding: 34px 40px 5px 40px;
  border-radius: 3px;
  background-color: #00b2ff;
  transform: translateX(-8px);
}
.modal__close-btn {
  position: absolute;
  top: 18px;
  right: 16px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
@media (hover: hover) {
  .modal__close-btn:hover:not(.focus-visible) {
    opacity: 0.7;
  }
  .modal__close-btn:hover:not(.focus-visible):active {
    opacity: 1;
  }
}
.modal__close-btn.focus-visible:focus {
  opacity: 0.7;
}
.modal__close-btn.focus-visible:focus:active {
  opacity: 1;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.input-message {
  margin: 0;
}

.header {
  position: fixed;
  z-index: 2;
  width: 100%;
  padding: 30px 40px 35px;
  background-image: linear-gradient(180deg, #041827 84.9%, rgba(4, 24, 39, 0) 100%);
}
@media (max-width: 1023px) {
  .header {
    position: static;
    padding: 20px 0 25px;
  }
}
@media (max-width: 767px) {
  .header {
    padding: 15px 0 20px;
  }
}

.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 1220px;
}
@media (max-width: 1023px) {
  .header__container {
    align-items: start;
  }
}

.header__button-wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  max-width: 330px;
}
@media (max-width: 1023px) {
  .header__button-wrapper {
    display: none;
  }
}

.header__button {
  min-width: 160px;
  margin-right: 61px;
  padding: 10px 17px 13px;
  border: 1px solid #f2f5f6;
  color: #f2f5f6;
  background-color: transparent;
}
@media (max-width: 1023px) {
  .header__button {
    display: none;
  }
}

.header__logo {
  width: 215px;
  flex-shrink: 0;
}
@media (max-width: 767px) {
  .header__logo {
    width: 35px;
    margin-right: 20px;
  }
}
@media (max-width: 767px) {
  .header__logo img {
    width: 35px;
    height: 35px;
  }
}

.contacts__link {
  display: flex;
  align-items: center;
  color: #ffffff;
  cursor: pointer;
  min-width: 218px;
}
@media (hover: hover) {
  .contacts__link:hover:not(.focus-visible) {
    opacity: 0.7;
  }
  .contacts__link:hover:not(.focus-visible):active {
    opacity: 1;
  }
}
.contacts__link.focus-visible:focus {
  opacity: 0.7;
}
.contacts__link.focus-visible:focus:active {
  opacity: 1;
}
@media (max-width: 1023px) {
  .contacts__link {
    padding: 10px;
    place-content: center;
    grid-template-columns: 1fr;
    min-width: auto;
  }
  .contacts__link span:not(.visually-hidden) {
    display: none;
  }
}
@media (max-width: 767px) {
  .contacts__link {
    padding: 5px 10px 10px;
  }
}
.contacts__link svg {
  margin-right: 15px;
  color: #00b2ff;
}
@media (max-width: 1023px) {
  .contacts__link svg {
    margin-right: 0;
  }
}

.contacts__list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 38px;
  max-width: 480px;
  width: 48%;
  margin-left: 8px;
}
@media (max-width: 1023px) {
  .contacts__list {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    justify-items: flex-end;
    width: auto;
    min-width: auto;
    margin-right: 20px;
    margin-left: 0;
  }
}
@media (max-width: 767px) {
  .contacts__list {
    margin-right: 10px;
  }
}

.intro {
  position: relative;
  background-color: #041827;
}
.intro h1 {
  margin: 0 0 34px;
  font-family: "Roboto-Condensed", "Arial", sans-serif;
  font-size: 80px;
  font-weight: 700;
  color: #f2f5f6;
  line-height: 94px;
  text-transform: uppercase;
  letter-spacing: 0.07em;
}
@media (max-width: 1023px) {
  .intro h1 {
    margin: 0;
    font-size: 64px;
    line-height: 75px;
  }
}
@media (max-width: 767px) {
  .intro h1 {
    margin: 0 0 6px;
    font-size: 36px;
    line-height: 43px;
  }
}
.intro p {
  margin: 0 0 48px;
  font-size: 22px;
  color: #f2f5f6;
  line-height: 35px;
}
@media (max-width: 1023px) {
  .intro p {
    margin: 0 0 59px;
  }
}
@media (max-width: 767px) {
  .intro p {
    margin: 0 0 43px;
    font-size: 14px;
